const navTog = document.querySelector('.nav-toggle')
const navMother = document.querySelector('.nav-mother')
let navVis = false;

if(navTog && navMother) {
    navTog.addEventListener('click', function(){
        navVis = !navVis

        navMother.style.transform = 'translateX(0)'
        if(navVis) {
        } else {
            navMother.style.transform = 'translateX(-100%)'
        }

        console.log(navVis)
    })
}

const toggleCollapse = document.querySelector('.toggle-collapse')
let collapsed = false

if(toggleCollapse){
    toggleCollapse.addEventListener('click', function(){
        collapsed = !collapsed

        const navItemLabels = document.querySelectorAll('.nav-item-label')
        const navHeaders = document.querySelectorAll('.nav-header')
        const collapseItems = [
            ...navItemLabels,
            ...navHeaders
        ]

        const navGroups = document.querySelectorAll('.nav-group')

        const appWrap = document.querySelector('.app-wrap')
        const nav = document.querySelector('nav')
        const mainWrap = document.querySelector('.main-wrap')
        const logoImage = document.querySelector('.logo-image')
        const iconImage = document.querySelector('.icon-image')

        if(collapsed) {
            for (let nil = 0; nil < collapseItems.length; nil++) {
                const item = collapseItems[nil];
                item.classList.add('hidden')

                if(item.previousElementSibling) {
                    const icon = item.previousElementSibling
                    icon.style.marginRight = '0'
                }
            }

            for (let ng = 0; ng < navGroups.length; ng++) {
                const ngroup = navGroups[ng];
                ngroup.style.marginBottom = '0rem'
                ngroup.style.paddingBottom = '.5rem'
            }

            toggleCollapse.style.transform = 'rotate(180deg)'

            appWrap.classList.add('flex')
            appWrap.classList.remove('grid', 'grid-cols-12', 'auto-rows-min')

            nav.classList.add('w-auto')
            nav.classList.remove('col-span-12', 'md:col-span-4', 'lg:col-span-3')

            logoImage.classList.add('hidden')
            iconImage.classList.remove('hidden')

            mainWrap.classList.add('w-full')
            mainWrap.classList.remove('col-span-12', 'md:col-span-8', 'lg:col-span-9')
        } else {
            for (let nil = 0; nil < collapseItems.length; nil++) {
                const item = collapseItems[nil];
                item.classList.remove('hidden')

                if(item.previousElementSibling) {
                    const icon = item.previousElementSibling
                    icon.style.marginRight = '.5rem'
                }
            }

            for (let ng = 0; ng < navGroups.length; ng++) {
                const ngroup = navGroups[ng];
                ngroup.style.marginBottom = '2rem'
                ngroup.style.paddingBottom = '1rem'
            }

            toggleCollapse.style.transform = 'rotate(0deg)'

            appWrap.classList.remove('flex')
            appWrap.classList.add('grid', 'grid-cols-12', 'auto-rows-min')

            nav.classList.remove('w-auto')
            nav.classList.add('col-span-12', 'md:col-span-4', 'lg:col-span-3')

            logoImage.classList.remove('hidden')
            iconImage.classList.add('hidden')

            mainWrap.classList.remove('w-full')
            mainWrap.classList.add('col-span-12', 'md:col-span-8', 'lg:col-span-9')
        }
    })
}